// Colors

$white: #fff;
$font-color: #777;
$primary-color: #58e1c1;
$secondary-color: #51ddfc;

$btn-default: #51ddfc;
$btn-error: #e4757a;
$btn-info: #927bc1;
$btn-success: #63cc82;
$btn-warning: #fd7856;

$btn-default-light: lighten($btn-default, 15%);
$btn-error-light: lighten($btn-error, 15%);
$btn-info-light: lighten($btn-info, 15%);
$btn-success-light: lighten($btn-success, 15%);
$btn-warning-light: lighten($btn-warning, 15%);

// Color-Maps

$col-colors: (
    'col-1': #927bc1,
    'col-2': #fd7856,
    'col-3': #deb2ee,
    'col-4': #7ee6fd,
    'col-5': #88d9a0,
    'col-6': #4da2bb,
    'col-7': #ec9fa3,
    'col-8': #fd9f85,
    'col-9': #e4757a,
    'col-10': #63cc82,
    'col-11': #51ddfc,
    'col-12': #80e8d1,
);

$btn-colors: (
    '.default': $btn-default,
    '.error': $btn-error,
    '.info': $btn-info,
    '.success': $btn-success,
    '.warning': $btn-warning,
);

$hover-btn: (
    '.default:hover': $btn-default-light,
    '.error:hover': $btn-error-light,
    '.info:hover': $btn-info-light,
    '.success:hover': $btn-success-light,
    '.warning:hover': $btn-warning-light,
);

// Sizes

$m-width: 1400px;

$m-flex-sizes: (
    '.col-1': 8.33%,
    '.col-2': 16.67%,
    '.col-3': 25.00%,
    '.col-4': 33.33%,
    '.col-5': 41.67%,
    '.col-6': 50.00%,
    '.col-7': 58.33%,
    '.col-8': 66.67%,
    '.col-9': 75.00%,
    '.col-10': 83.33%,
    '.col-11': 91.67%,
    '.col-12': 100%,
);

// Media Query

$brk-m: 'min-width: 768px';